@value makerLightGrey, makerPink, makerLightPink, makerGreen, makerLightGreen, makerLime from '../../common/colors.css';
@value mainFont from '../../common/size.css';

.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.input {
    display: none;
}

.button {
    width: 100%;
    height: 100%;
    font-weight: bold;
    cursor: pointer;
    border: 0;
    outline: 0;
    font-family: mainFont;
    font-size: 0.8rem;
}

.button.grey {
    background-color: makerLightGrey;
}

.button.lightGreen {
    background-color: makerLime;
}

.button:hover {
    opacity: 0.8;
}

.loading {
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    border: 3px solid makerLightGrey;
    border-top: 3px solid makerPink;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}